import React from "react";
import "../css/Work.css";

function Work(props) {
  let workData = props.workData;

  const populateWork = () => {
    return workData.map((project) => {
      return (
        <div className="project-item" key={project.title}>
          <div className="project-title">
            <p>{project.title}</p>
          </div>
          <div className="project-image">
            <img
              src={require("../img/work/" + project.image)}
              alt="project-thumb"
            />
          </div>
          <div className="project-description">
            <p>{project.description}</p>
          </div>

          <div className="project-buttons-container">
            <button className="project-button button is-white">
              <a
                target="_blank"
                rel="noopener noreferrer"
                href={project.url}
                className="project-button-text"
              >
                View
              </a>
            </button>
            <button className="project-button button is-white">
              <a
                target="_blank"
                rel="noopener noreferrer"
                href={project.repo}
                className="project-button-text"
              >
                Github
              </a>
            </button>
          </div>
        </div>
      );
    });
  };

  return (
    <div id="work" className="section-container">
      <div id="work-container">
        <h1 className="section-title">Projects</h1>
        <div id="work-section">
          <div className="project-container">{populateWork()}</div>
        </div>
      </div>
    </div>
  );
}

export default Work;
